import Tab from "../../../../components/Tab/Tab"
import DeriNumberFormat from "../../../../utils/DeriNumberFormat"
import Search from "../../../../components/Search/Search"
import './symbolPanel.less'
import { inject, observer } from "mobx-react"
import Type from "../../../../model/Type"
import classNames from 'classnames'
import {
  Accordion,
  AccordionItem,
  AccordionItemHeading,
  AccordionItemButton,
  AccordionItemPanel,
} from 'react-accessible-accordion';
import Icon from "../../../../components/Icon/Icon"
import ReactTooltip from "react-tooltip"
import { useMemo ,useState,useEffect} from "react"
import Category from "../../../../model/Category"


function SymbolPanel({ trading, lang, symbols = [] ,onClose }) {
  const onSelect = info => {
    if (info.version === 'v2_lite_open') {
      return;
    }
    trading.onSymbolChange(info)
    onClose && onClose()
  }
  return (
    <div className='symbol-panel'>
      <div className='sp-main'>
        {Type.isFuture && Category.isFutures
          ?
            <FuturePanel lang={lang} symbols={symbols} trading={trading} onSelect={onSelect} />
          : 
          Type.isOption && Category.isOption ?
            <OptionPanel symbols={symbols} onSelect={onSelect} />
          :
            <PowerPanel symbols={symbols} onSelect={onSelect} lang={lang}/>
        }
      </div>
    </div>
  )
}

function FuturePanel({ lang, symbols = [], onSelect }) {
  const tabSet = useMemo(() => (
    {
      [lang['main']]: {
        content: <SymbolList lang={lang} symbols={symbols} zone='main' onSelect={onSelect} />
      },
      [lang['inno']]: {
        content: <SymbolList lang={lang} symbols={symbols} zone='inno' onSelect={onSelect} />
      },
      [lang['open']]: {
        content: <SymbolList lang={lang} symbols={symbols}  zone='open' onSelect={onSelect} />
      }
    }
  ),[lang,symbols]) 
  return (
    <Tab tabSet={tabSet} activeBgColor='rgba(255, 255, 255, 0.1)' className='future-zone-tab' />
  )
}

const OptionPanel = ({symbols = {}, onSelect }) => {
  const keys = Object.keys(symbols);
  const tabSet = useMemo(() => {
    const total = {}
    keys.reduce((total,key) => {
      total[key] = {
        content : <SymbolList symbols = {symbols[key]} zone={key} onSelect = {onSelect}/> ,
        icon : key,
        inicon : key,
        iconWidth : 20,
        iconHeight : 20,
        iconMargin : 16
      }
      return total;
    },total)
    return total;
  },[symbols])
  return (
    <Tab tabSet = {tabSet} iconMarginRight='8' activeBgColor='rgba(255, 255, 255, 0.1)' className='option-panel-tab'/>
  )
}

const PowerPanel = ({lang , symbols = {},onSelect}) => {
  return <SymbolList symbols={symbols} onSelect={onSelect} zone = 'inno' lang={lang}/>
}


function SymbolList({ symbols, version, onSelect, lang ,zone}) {
  const [filterSymbols, setFilterSymbols] = useState([])
  const openSymbols = useMemo(() => (
    [
      {
        "symbol": "BTCUSD",
        "version":"v2_lite_open",
        "displaySymbol": "BTCUSDT/SATS",
      },
      {
        "symbol": "BTCUSD",
        "version":"v2_lite_open",
        "displaySymbol": "BTCUSDT/ONT",
      },
      {
        "symbol": "ETHUSD",
        "version":"v2_lite_open",
        "displaySymbol": "ETHUSDT/ONT",
      },
      {
        "symbol": "ONTUSD",
        "version":"v2_lite_open",
        "displaySymbol": "ONTUSD/ONT",
      },
      {
        "symbol": "BTCUSD",
        "version":"v2_lite_open",
        "displaySymbol": "BTCUSDT/NULS",
      },
      {
        "symbol": "ETHUSD",
        "displaySymbol": "ETHUSDT/NULS",
      },
      {
        "symbol": "NULSUSDT",
        "version":"v2_lite_open",
        "displaySymbol": "NULSUSDT/ONT",
      },
      {
        "symbol": "BTCUSD",
        "version":"v2_lite_open",
        "displaySymbol": "BTCUSDT/zUSD",
      },
      {
        "symbol": "ETHUSD",
        "version":"v2_lite_open",
        "displaySymbol": "ETHUSDT/zUSD",
      },
    ]),[])
  useEffect(() => {
    if (zone === 'open') {
      setFilterSymbols(openSymbols)
    } else {
      setFilterSymbols(symbols.sort((s1,s2) => s1.version > s2.version ? -1 : 0).filter(s => s.zone.indexOf(zone) > -1))
    }
  }, [symbols,zone])

  return (
    <div className='symbols'>
      <Search list={filterSymbols} key={zone} width='299' height='48' searchName='symbol' onChange={list => setFilterSymbols(list) } className='symbol-search' />
      <div className='s-body'>
        {filterSymbols.map((info, index) => {
          const volume24Class = classNames('volume24',{up : info.volumn24h > 0 ,down : info.volumn24h < 0})
          return (
            <div className='symbol-row' key={index} onClick={() => onSelect(info)}>
              <div className='ss-name'><Icon token={info.symbol} height={20} width={20} />{info.displaySymbol}</div>
              <div className='ss-value'>
                {zone === 'open' && <>
                  <Open lang={lang} />
                </>}
                {zone !== 'open' && <>
                  <div className='mark-price'><DeriNumberFormat prefix='$' value={info.markPrice} decimalScale={2}  /></div>
                  <div className='volume24-changed'>
                    <div className='direction'>{info.volumn24h && <Icon width={8} token={info.volumn24h > 0 ? 'rate-up' : 'rate-down'} />}</div>
                    <div className={volume24Class}><DeriNumberFormat value={info.volumn24h * 100} decimalScale={2} suffix='%' /></div>
                  </div>
                </>}
              </div>
            </div>)
        })}
      </div>
    </div>
  )
}

function Open({ lang }) {
  return (
    <div className='open-list'>
      {lang['operating']}
    </div>
  )
}

export default inject('trading')(observer(SymbolPanel))